import React from 'react'
import Circle from '../../components/UI/circle/Circle'
import {css, StyleSheet} from 'aphrodite/no-important'
const styles = StyleSheet.create({
    page :{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        boxSizing: 'border-box',
        textDecoration: 'none',
    },
    page_title_blue: {
        marginTop: "40px",
        fontStyle: "normal",
        fontWeight: 700,
        fontSize: "24px",
        lineHeight: "30px",
        color: "#0196FF"
      }
})
function BetweenPage(props) {
    return (
        <div className={css(styles.page)} style={{paddingTop: '100px'}}>
            <Circle url={'data:image/svg+xml;base64,'}/>
            <h3 className={css(styles.page_title_blue)}>{props.text}</h3>
        </div>
      )
  
}

export default BetweenPage